.loader {
  margin: auto;
  height: 150px;
  width: 150px;
  margin-top: 45px;
}

.loader > div {
  height: inherit;
  width: inherit;
  position: absolute;
}

.loader > div > div {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background: #999;
  position: absolute;
  top: 0%;
  right: 50%;
  transform: translate(50%, 0%) scale(0);
  animation-name: scale;
  animation-duration: 1.1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

.loader > div:nth-child(2) {
  transform: rotate(30deg);
}

.loader > div:nth-child(3) {
  transform: rotate(60deg);
}

.loader > div:nth-child(4) {
  transform: rotate(90deg);
}

.loader > div:nth-child(5) {
  transform: rotate(120deg);
}

.loader > div:nth-child(6) {
  transform: rotate(150deg);
}

.loader > div:nth-child(7) {
  transform: rotate(180deg);
}

.loader > div:nth-child(8) {
  transform: rotate(210deg);
}

.loader > div:nth-child(9) {
  transform: rotate(240deg);
}

.loader > div:nth-child(10) {
  transform: rotate(270deg);
}

.loader > div:nth-child(11) {
  transform: rotate(300deg);
}

.loader > div:nth-child(12) {
  transform: rotate(330deg);
}

.loader > div:nth-child(2) > div {
  animation-delay: 0.1s;
}

.loader > div:nth-child(3) > div {
  animation-delay: 0.2s;
}

.loader > div:nth-child(4) > div {
  animation-delay: 0.3s;
}

.loader > div:nth-child(5) > div {
  animation-delay: 0.4s;
}

.loader > div:nth-child(6) > div {
  animation-delay: 0.5s;
}

.loader > div:nth-child(7) > div {
  animation-delay: 0.6s;
}

.loader > div:nth-child(8) > div {
  animation-delay: 0.7s;
}

.loader > div:nth-child(9) > div {
  animation-delay: 0.8s;
}

.loader > div:nth-child(10) > div {
  animation-delay: 0.9s;
}

.loader > div:nth-child(11) > div {
  animation-delay: 1s;
}

.loader > div:nth-child(12) > div {
  animation-delay: 1.1s;
}

@keyframes scale {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

.file-not-found {
  text-align: center;
  padding-top: 15%;
}

.cs-checkbox input[type='checkbox'].cs + .lbl,
.cs-checkbox input[type='radio'].cs + span {
  position: relative;
  display: inline-block;
  min-height: 18px;
  min-width: 18px;
  cursor: pointer;
}
.cs-checkbox input[type='checkbox'].cs,
.cs-checkbox input[type='radio'].cs {
  visibility: hidden;
  position: absolute;
  z-index: 20;
  width: 18px;
  height: 18px;
}
.cs-checkbox input[type='checkbox'].cs + .lbl::before {
  cursor: pointer;
  font-family: fontAwesome;
  font-weight: 400;
  font-size: 12px;
  color: #25c2a3;
  content: '\a0';
  background-color: #dfeaec;
  border-radius: 0;
  display: inline-block;
  text-align: center;
  height: 15px;
  line-height: 14px;
  min-width: 15px;
  margin-right: 8px;
  position: relative;
  top: -1px;
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  border-radius: 1px;
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding;
  background-clip: padding-box;
  margin-top: 1.4px;
}
